@import url(https://fonts.googleapis.com/css?family=Fira+Code&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 100vh;
}

html,
body {
  font-family: 'Fira Code', monospace;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-image: url(/groupsifce/static/media/background.0bf28038.jpg);
}

main {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  min-height: 100px;
  background-color: cadetblue;
  padding: 10px;
}

.header h1 {
  font-family: 'Fira Code', monospace;
  color: #FFF;
}

.header a {
  text-decoration: none;
  cursor: pointer;
  border-radius: 5px;
  padding: 3px 12px;
  background-color: honeydew;
  color: green;
  align-self: flex-end;
  -webkit-transition: .2s;
  transition: .2s;
}

.header a:hover {
  margin-right: 10px;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.container {
  flex-grow: 1;
}

div.card-list {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.footer {
  display: block;
  width: 100%;
  background-color: grey;
  color: white;
  text-align: right;
  padding: 10px;
}

.footer span{
  display: inline-block;
}

.footer a {
  cursor: pointer;
  text-decoration: none;
  color: #FFF;
  font-weight: bold;
  display: inline-block;
}

.footer a img {
  width: 25px;
  position: relative;
  top: 7px;
  margin: 0 0 0 15px;
}



@media (max-width: 768px) {
  div.card-list {
    justify-content: center;
  }
}
.card {
    border: 1px solid #eee;
    border-radius: 5px;
    height: 14rem;
    background-color: white;
    width: 15rem;
    float: left;
    margin: 10px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    -webkit-transition: .3s;
    transition: .3s;
}

.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.card img {
    width: 120px;
    height: 120px;
    border-radius: 100px;
    border: 1px solid #eee;
}

.card h1 {
    color: green;
    font-size: 16px;
    align-self: flex-end;
}

@media only screen and (max-width: 600px) {
    .card {
        width: 20rem;
    }
}
